var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"article_1"},[_c('Navigation'),_c('div',{staticClass:"article_header"},[_c('div',{staticClass:"col_1"},[(_vm.$global.global_ru)?_c('h1',[_vm._v("ДОСТУП К ИНФОРМАЦИИ")]):_vm._e(),(_vm.$global.global_kz)?_c('h1',[_vm._v("АҚПАРАТҚА ҚОЛ ЖЕТКІЗУ")]):_vm._e(),(_vm.$global.global_ru)?_c('h2',[_c('span',[_vm._v("ЧТО ИЗМЕНИЛОСЬ:")])]):_vm._e(),(_vm.$global.global_kz)?_c('h2',[_c('span',[_vm._v("Не өзгерді:")])]):_vm._e(),(_vm.$global.global_ru)?_c('img',{attrs:{"alt":"","src":require("../assets/pic/dostup_k_informacii_1.jpg")}}):_vm._e(),(_vm.$global.global_kz)?_c('img',{attrs:{"alt":"","src":require("../assets/pic/kz_dostup_k_informacii_1.png")}}):_vm._e()]),_vm._m(0)]),_c('div',{staticClass:"text_container"},[_c('div',{staticClass:"mobile_only"},[(_vm.$global.global_ru)?_c('h2',[_c('span',[_vm._v("ЧТО ИЗМЕНИЛОСЬ:")])]):_vm._e(),(_vm.$global.global_kz)?_c('h2',[_c('span',[_vm._v("Не өзгерді:")])]):_vm._e(),(_vm.$global.global_ru)?_c('img',{attrs:{"alt":"","src":require("../assets/pic/dostup_k_informacii_1.jpg")}}):_vm._e(),(_vm.$global.global_kz)?_c('img',{attrs:{"alt":"","src":require("../assets/pic/kz_dostup_k_informacii_1.png")}}):_vm._e()]),(_vm.$global.global_ru)?_c('div',[_vm._m(1),_c('p',[_vm._v("В основном, журналисты отмечают, что ситуация с доступом к информации с введением поправок ухудшилась, «несмотря на громкие популистские заявления о \"слышащем государстве\".")]),_c('p',[_vm._v("Разделение информации на официальные сообщения и другие не принесло ничего, кроме путаницы в определениях, сроках и типах информации. Многие журналисты отметили, что чиновники стали использовать эту норму в свою пользу – как аргумент для сокрытия информации. «Государственные органы стали более закрытыми, осторожными» - отмечают респонденты. «Чиновники стали очень чувствительные – не хотят общаться, а потом журналист думает – лучше буду с ним дружить, чем ссориться, чтобы он хоть что-то дал». ")]),_c('p',[_vm._v("Зачастую представители госорганов мало знают о законе о СМИ и действующем медиа-законодательстве. Журналисты говорят, что к запросам стали относится более безответственно. Практически все отмечают, что лучше всего работают «личные связи и контакты», альтернативные источники. ")]),_c('p',[_vm._v("«Ругаться бесполезно, нам нужная оперативная информация, поэтому просто подключаем собственные связи» - говорят они. Многие уверены, что доступ к информации – это вопрос профессионализма каждого журналиста. А профессионализм заключается в мастерстве установить личные связи.")]),_c('p',[_vm._v("Учитывая сложный период в связи с распространением COVID19 и всплеском заболеваемости вирусной пневмонией, журналистам стало сложнее контактировать с компетентными органами, брифинги проходят в онлайн-режиме, не на все вопросы специалисты дают исчерпывающие ответы, а сведения о точном количестве заболевших и умерших от коронавируса не разглашаются. ")]),_vm._m(2),_vm._m(3),_c('p',[_vm._v("Некоторые ссылаются на соцсети (предлагают брать информацию оттуда).")]),_c('p',[_vm._v("Одна из главных наблюдений: если с ведомством дружишь, то будешь получать информацию вовремя. Поэтому журналисты стараются не ссориться с чиновниками. Обычно по знакомству отвечают устно оперативно или в мессенджерах. ")]),_c('p',[_vm._v("В Караганде пресс-службы отвечают на запрос одной редакции всем сразу, в рассылке, таким образом нарушая авторство темы. Иногда просят принести запрос нарочно (областная СЭС Караганды). Некоторые требуют фирменный бланк, обязательно напечатанные реквизиты. Бывают ситуации, когда республиканским СМИ информацию дают сначала, а региональные публикуют после. Журналисты считают это некорректным, поскольку информация о событии должна появляться там, где это произошло. ")]),_c('p',[_vm._v("Также журналисты отмечают, что госорганы регулярно нарушают сроки, забывают про два дня, которые обозначены в законе. Иногда приходится писать повторно, используя все средства связи: социальные сети, запросы, телефонные звонки. Нередко просят предоставить материал на согласование перед публикацией. ")]),_c('p',[_vm._v("Многие общаются только через пресс-службу, особенно по вопросам, связанным с финансами (акимат ЮКО), требуют материалы на согласование. Что касается профессионализма пресс-служб, то там, где работают бывшие журналисты, дела идут неплохо. У кого в штате их нет вообще, информацию получить очень сложно, потому что сотрудники вынуждены выполнять не свои функции и обязанности. Текучесть в пресс-службах очень высокая. Особенные трудности у ТВ журналистов. Они жалуются на нежелание спикеров говорить на камеру, отсутствие адекватных экспертов. ")]),_c('p',{staticClass:"qoute"},[_vm._v("«Нет системы, все зависит от личности. Какой начальник – так и будут давать информацию». ")]),_c('p',{staticClass:"qoute"},[_vm._v("«Пресс-секретарь не должен допускать негативных материалов в СМИ – такая у них установка». ")]),_c('p',{staticClass:"qoute"},[_vm._v("«Пришел новый аким, все запрещает. Отдел внутренней политики решает, какую информацию предоставить, а какую нет. Главный принцип – не навредит ли это имиджу города? Часто просто не хотят отвечать».")]),_c('p',{staticClass:"qoute"},[_vm._v("«Написали запрос в миграционную службу – в ответ тишина. И только после того, как озвучили ситуацию в общем чате всех пресс-секретарей в ватсап – отреагировали моментально».")]),_c('p',{staticClass:"qoute"},[_vm._v("«СМИ перестали быть властью, у них нет авторитета. Все держится на личных связях». ")]),_c('p',{staticClass:"qoute"},[_vm._v(" «Госорганы учат, как грамотно отмазаться от журналистов и послать их, а не общаться и предоставлять информацию». ")]),_c('p',[_vm._v("Многие отмечают закрытость местных правоохранительных органов. Например, информацию о криминальном событии часто получают из МВД, при этом местные ДВД, прокуратура, антикоррупционное ведомство – молчат. Ключевая проблема – необходимость для ДВД согласовывать с прокуратурой любую информацию для СМИ. Это усложняет процесс, увеличивает время, в итоге журналисты не получают нужную им информацию. «Некоторые не берут трубку, заносят номера телефонов в черный список». ")]),_c('p',[_vm._v("ДВД в основном отвечают очень коротко, без подробностей, аргументируя это «тайной следствия». ")]),_c('p',[_vm._v("Казахоязычные журналисты отметили, что на местах созданы региональные службы коммуникации, их материалы тиражируются, журналисты привыкли к готовым текстам. Это неправильно и развращает журналистов.")]),_c('p',[_vm._v("Не все журналисты пользуются системой егов, особенно работающие в региональных газетах. У многих нет ЭЦП, и они не видят смысла в том, чтобы использовать ее для получения информации. В некоторых регионах журналисты отмечают, что ватсапп – главный способ связи. Также нередко связываются с чиновниками через инстаграм и фб. ")]),_c('p',[_vm._v("Многие отмечают, что сайты государственных органов обновляются крайне редко. ")]),_vm._m(4),_c('p',[_vm._v("Большинство журналистов предпочитают не реагировать на отказы – не хватает времени, сильная загруженность, не хотят волокиты, не верят, что это может принести результат. Таким образом, очень редко пишут жалобы, практически никогда не обращаются с заявлениями в суды. ")]),_c('p',[_vm._v("Одним из способов добыть информацию является своего рода шантаж, когда журналисты говорят, что напишут в материале о непредоставлении информации либо укажут личные контакты человека, который отказался ее предоставлять. Также журналисты обращают внимание чиновников на то, что информация уже появилась в социальных сетях, поэтому смысла скрывать ее нет. ")]),_c('p',[_vm._v("Многие решают вопросы через официальные станицы в ФБ, где можно отметить начальника, и тогда дело сдвигается с мертвой точки. ")]),_c('p',[_vm._v("Самая чувствительная информация: финансовая, по неосвоению средств, тендеры, строительство, проверки тендеров, государственный аудит, недропользования, информация из маслихата, инспекция труда, экологическая, религиозная (из КНБ).")]),_c('p',[_vm._v("Некоторые отмечали, что «если есть авторитет, если ты ведешь себя спокойно, соблюдаешь правила игры – то информацию дают в срок и по закону».")]),_vm._m(5)]):_vm._e(),_vm._v(" "),(_vm.$global.global_kz)?_c('div',[_vm._m(6),_c('p',[_vm._v("Негізінен, журналистердің айтуынша, «құлақ асатын мемлекет туралы» популистік мәлімдемелерге қарамастан», заңға өзгерту енгізілгелі ақпаратқа қол жеткізу жағдайы нашарлап кетті. ")]),_c('p',[_vm._v("Ақпараттық ресми хабарламалар мен басқаларға бөлінуі олардың анықтамаларын, уақыт пен ақпарат түрлерін шатастырудан басқа ештеңе берген жоқ. Көптеген журналистердің айтуынша, шенеуніктер бұл норманы ақпарат жасыруда аргумент ретінде өз пайдаларына қолданып отыр. «Мемлекеттік органдар неғұрлым жабық, сақтанып отырады» - деп атап өтті респонденттер. «Шенеуніктер өте сезімтал бола бастады, байланысқа шыққылары келмейді, сондықтан журналист онымен дауласқанша, қандай болса да ақпарат алу үшін дос болу керек деп ойлайды». ")]),_c('p',[_vm._v("Көп жағдайда мемлекеттік органдардың өкілдері БАҚ туралы заң және қолданыстағы медиа заңнамалар туралы көп біле бермейді. Журналистер олардың сұраным хаттарға жауапсыздықпен қарайтынын алға тартады. Барлығы дерлігі «жеке таныстық пен байланыстың», балама ақпарат көздерінің жұмыс істейтінін атап өтті. «Сөзге келудің пайдасы болмайды, бізге жедел ақпарат керек, сондықтан, жеке байланыстарды іске қосамыз» - дейді олар. Көпшілігі ақпаратқа қол жеткізуді әрбір журналистің кәсібилілігі дегенге сенімді. Ал кәсібилілік жеке байланыстар орнату шеберлігіне де қатысты. ")]),_c('p',[_vm._v("COVID19-дың таралуы және вирусты өкпе қабынуының өршуіне байланысты журналистер құзырлы органдармен байланыс орнатуда қиындықтарға кезікті, брифингтер онлайн режимде өтеді, мамандар барлық сұраққа бірдей жауап бермейді, коронвирусты қанша адамның жұқтырғаны, қанда адамның өлгені туралы нақты мәлімет айтылмады.")]),_vm._m(7),_vm._m(8),_c('p',[_vm._v("Кейбірі әлеуметтік желілерге сілтейді (сол жақтан ақпарат алуға кеңес береді).")]),_c('p',[_vm._v("Журналистер байқаған тағы бір жайт: егер ведомствомен тату болсаң, онда ақпаратты уақытында аласың. Сондықтан, журналистер шенеуніктермен сөзге келмеуге тырысады. Әдетте таныстықты пайдаланып, ауызша немесе мессенджер жедел ақпарат алуға болады. ")]),_c('p',[_vm._v("Қарағандыда баспасөз қызметі бір редакцияның сұраным хатына әзірленген жауапты барлығына таратып жібереді, осылайша тақырыптың авторлығына нұқсан келтіреді. ")]),_c('p',[_vm._v("Кейде сұраным хатты қолма-қол әкеліп тапсыруды сұрайды (Қарағанды облыстық СЭС). Бірқатары міндетті түрде реквизиттері көрсетілген фирмалық бланк талап етеді. ")]),_c('p',[_vm._v("Журналистер сондай-ақ мемлекеттік органдардың заңда көрсетілген екі күнді естен шығарып, мерзімді жиы бұзатынын атап өтті. Кейде байланыс құралдарының барлық түрі – әлеуметтік желілер, сұраным, телефон қоңырауларын пайдалана отырып, қайтадан сұрау салу керек болатынын алға тартады. Кейде тіпті жарияламастан бұрын материалды тексеріп алу үшін сұрайтындар бар. ")]),_c('p',[_vm._v("Кейде ақпаратты республикалық БАҚ алғаш болып жариялайды, ал аймақтық ақпарат құралдары біраз уақыттан кейін тарата бастайды. Журналистердің пайымынша, бұл дұрыс емес, ақпарат алдымен оқиға болған жерде таралуы керек. ")]),_c('p',[_vm._v("Шенеуніктердің дені, әсіресе қаржыға байланысты сұрақ болса (Түркістан облысы (ОҚО) әкімдігі), тек баспасөз қызметі арқылы байланысқа шығады, материалды мақұлдату керек деп талап қояды. Баспасөз қызметінің кәсібилілігіне келсек, егер ол қызметте бұрынғы журналистер отырса, жұмыс жақсы жүреді. Штатта ондайлар болмаса, ақпарат алу өте қиын, өйткені қызметкерлер өз қызметтері және міндеттерімен айналыспайды. Баспасөз қызметтерінде кадрлар жиі ауысады. ")]),_c('p',[_vm._v("ТВ журналистері қатты қиналады. Айтуларынша, спикерлер камера алдында сөйлегісі келмейді, адекватты сарапшылар жоқтың қасы. ")]),_c('p',{staticClass:"qoute"},[_vm._v("«Жүйе жоқ, барлығы жеке ад